import './zamer-stolbiki.css';
import React from 'react';
import Asaid from '../../components/Asaid/Asaid';
import { assets } from '../../assets/assets';

function ZamerStolb24() {
  return (
    <div className="main">
      <div className="main-inner">
        <Asaid />
        <aside className="aside-right">
          <h3 class="title-example"> </h3>
          <div className="product-cart">
            <div className="product-cart-left">
            <img
                className="photo"
                src={assets.ZamerStolb24}
                style={{ height: 250, width: 150 }}
              />
            </div>
            <div className="product-cart-right">
              <h4 className="zhbi-tittle-main">   2,4 </h4>
              <p className="zhbi-tittle-main-h3">:</p>
              <p className="main-text-p"> , 240 </p>
              <p className="main-text-p">  , 12 </p>
              <p className="main-text-p">   400</p>
              <p className="main-text-p"> , 14 </p>
              <br />
              <p className="zhbi-tittle-main">:  </p>
            </div>
          </div>
        </aside>
      </div>
    </div>
  );
}

export default ZamerStolb24;
