import './kolca.css';
import React from 'react';
import Asaid from '../../components/Asaid/Asaid';
import { assets } from '../../assets/assets';

function KolcaStenovie206() {
  return (
    <div className="main">
      <div className="main-inner">
        <Asaid />
        <aside className="aside-right">
          <h3 class="title-example"> </h3>
          <div className="product-cart">
            <div className="product-cart-left">
              <img src={assets.kolcaOpornoe} alt="" />
            </div>
            <div className="product-cart-right">
              <h4 className="zhbi-tittle-main">    20-6</h4>
              <p className="zhbi-tittle-main-h3">:</p>
              <p className="main-text-p">  1000 </p>
              <p className="main-text-p">  1900 </p>
              <p className="main-text-p">  220*200*60*10 </p>
              <br />
              <p className="zhbi-tittle-main">:  </p>
            </div>
          </div>
        </aside>
      </div>
    </div>
  );
}

export default KolcaStenovie206;
