import './plitka-pzk.css';
import React from 'react';
import Asaid from '../../components/Asaid/Asaid';
import { assets } from '../../assets/assets';

function PlitkaPZK() {
  return (
    <div className="main">
      <div className="main-inner">
        <Asaid />
        <aside className="aside-right">
          <h3 class="title-example">     </h3>
          <div className="product-cart">
            <div className="product-cart-left">
              <img src={assets.pzk} alt="" />
            </div>
            <div className="product-cart-right">
              <h4 className="zhbi-tittle-main"> </h4>
              <p className="zhbi-tittle-main-h3">:</p>
              <p className="main-text-p">    1.6 </p>
              <p className="main-text-p">   </p>
              <p className="main-text-p">   240 </p>
              <p className="main-text-p">   16 </p>
              <p className="main-text-p">   480 </p>
              <p className="main-text-p">      640</p>
              <p className="main-text-p">     </p>
              <br />
              <p className="zhbi-tittle-main">:  </p>
            </div>
          </div>
        </aside>
      </div>
    </div>
  );
}

export default PlitkaPZK;
