import './kolodec-zazemleniya.css';
import React from 'react';
import Asaid from '../../components/Asaid/Asaid';
import { assets } from '../../assets/assets';

function KvadratKolodec() {
  return (
    <div className="main">
      <div className="main-inner">
        <Asaid />
        <aside className="aside-right">
          <h3 class="title-example"> </h3>
          <div className="product-cart">
            <div className="product-cart-left">
              <img src={assets.KvadratKolodec} alt="" />
            </div>
            <div className="product-cart-right">
              <h4 className="zhbi-tittle-main">  ( d 10)</h4>
              <p className="zhbi-tittle-main-h3">:</p>
              <p className="main-text-p"> , 225*225*200*200*120 </p>
              <p className="main-text-p"> , 3500 </p>
              <p className="main-text-p"> , 4800 </p>
              <p className="main-text-p">   , </p>
              <br />
              <p className="zhbi-tittle-main">:  </p>
            </div>
          </div>
        </aside>
      </div>
    </div>
  );
}

export default KvadratKolodec;
